import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import postss from '../page/content/postss' //
import posts from '../page/content/posts' //
import user_list from '../page/content/user_list' //
import sign from '../page/sign'//
import settled_examine from '../page/content/settled_examine' //
import settled from '../page/content/settled' //
import postss_details from '../page/content/postss_details' //
import posts_details from '../page/content/posts_details' //
import settled_details from '../page/content/settled_details' //
import field from '../page/field/field' //
import field_add from '../page/field/field_add' //  
import field_modify from '../page/field/field_modify' // 
import user from '../page/user/user' // 
import user_details from '../page/user/user_details' //
import user_add from '../page/user/user_add' //  
import user_edit from '../page/user/user_edit' //  
import user_sign from '../page/user/user_sign' //  
import user_diction from '../page/user/user_diction' //  
import group from '../page/group/group' // 
import group_details from '../page/group/group_details' //
import group_add from '../page/group/group_add' // 
import group_edit from '../page/group/group_edit' // 
import group_grant from '../page/group/group_grant' // 
import cash from '../page/cash/cash' // 
import cash_examine from '../page/cash/cash_examine' // 
import menu from '../page/menu/menu' //  
import menu_add from '../page/menu/menu_add' // 
import  menu_details from '../page/menu/menu_details' // 
import  menu_edit from '../page/menu/menu_edit' //
import  fictitious_user from '../page/fictitious/fictitious_user' //
import  fictitious_txt from '../page/fictitious/fictitious_txt' //
import  fictitious_det from '../page/fictitious/fictitious_det' //
import  fictitious from '../page/fictitious/fictitious' //
import  fictitious_addtxt from '../page/fictitious/fictitious_addtxt' //





Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: 'sign',
      component: sign
    },
    //
    {
      path: 'home',
      name: 'home',
      children:[
        //
        {
          path: '/postss',
          name: 'postss',
          component: postss,
           meta:{isLogin:true}
        },
        //
        {
          path: '/posts',
          name: 'posts',
          component: posts,
            meta:{isLogin:true}
        },
        //
        {
          path: '/postss_details',
          name: 'postss_details',
          component: postss_details,
          meta:{isLogin:true},
        },
        //
        {
          path: '/posts_details',
          name: 'posts_details',
          component: posts_details,
          meta:{isLogin:true},
        },
        //
        {
          path: '/user_list',
          name: 'user_list',
          component: user_list,
          meta:{isLogin:true},
        },

        //
        {
          path: '/settled_examine',
          name: 'settled_examine',
          meta:{isLogin:true},
          component: settled_examine
        },
        //
        {
          path: '/settled_details',
          name: 'settled_details',
          meta:{isLogin:true},
          component: settled_details
        },
        //        
        {
          path: '/settled',
          name: 'settled',
          meta:{isLogin:true},
          component: settled
        },
        //      
        {
          path: '/field',
          name: 'field',
          meta:{isLogin:true},
          component: field
        },
        //     
        {
          path: '/field_modify',
          name: 'field_modify',
          meta:{isLogin:true},
          component: field_modify
        },
        // 
        {
          path: '/field_add',
          name: 'field_add',
          meta:{isLogin:true},
          component: field_add
        },
        // 
        {
          path: '/user',
          name: 'user',
          meta:{isLogin:true},
          component: user
        },
          //  
        {
          path: '/user_details',
          name: 'user_details',
          meta:{isLogin:true},
          component: user_details
        },
        // 
        {
          path: '/user_diction',
          name: 'user_diction',
          meta:{isLogin:true},
          component: user_diction
        },
        //
        {
          path: '/user_sign',
          name: 'user_sign',
          meta:{isLogin:true},
          component: user_sign
        },
          //
          {
            path: '/user_add',
            name: 'user_add',
            meta:{isLogin:true},
            component: user_add
          },
          // 
          {
            path: '/user_edit',
            name: 'user_edit',
            meta:{isLogin:true},
            component: user_edit
          },
        //  
        {
          path: '/group',
          name: 'group',
          meta:{isLogin:true},
          component: group
        },
        // 
        {
          path: '/group_details',
          name: 'group_details',
          meta:{isLogin:true},
          component: group_details
        },
        //  
        {
          path: '/group_add',
          name: 'group_add',
          meta:{isLogin:true},
          component: group_add
        },
        //  
        {
          path: '/group_edit',
          name: 'group_edit',
          meta:{isLogin:true},
          component: group_edit
        },
        //
        {
          path: '/group_grant',
          name: 'group_grant',
          meta:{isLogin:true},
          component: group_grant
        },
        //  
        {
          path: '/cash',
          name: 'cash',
          meta:{isLogin:true},
          component: cash
        },
        //  
        {
          path: '/cash_examine',
          name: 'cash_examine',
          meta:{isLogin:true},
          component: cash_examine
        },
        // 
        {
          path: '/menu',
          name: 'menu',
          meta:{isLogin:true},
          component: menu
        },
        // 
        {
          path: '/menu_add',
          name: 'menu_add',
          meta:{isLogin:true},
          component: menu_add
        },
        //  
        {
          path: '/menu_details',
          name: 'menu_details',
          meta:{isLogin:true},
          component:menu_details
        },
        //  
        {
          path: '/menu_edit',
          name: 'menu_edit',
          meta:{isLogin:true},
          component:menu_edit
        },
        // 
        {
          path: '/fictitious_txt',
          name: 'fictitious_txt',
          meta:{isLogin:true},
          component:fictitious_txt
        },
        // 
        {
          path: '/fictitious_user',
          name: 'fictitious_user',
          meta:{isLogin:true},
          component:fictitious_user
        },
        // 
        {
          path: '/fictitious_det',
          name: 'fictitious_det',
          meta:{isLogin:true},
          component:fictitious_det
        },
        //
        {
          path: '/fictitious',
          name: 'fictitious',
          meta:{isLogin:true},
          component:fictitious
        },
        //
        {
          path: '/fictitious_addtxt',
          name: 'fictitious_addtxt',
          meta:{isLogin:true},
          component:fictitious_addtxt
        },
      ],
      component: home
    },

    
  ]
})



