<template>
<!--  -->
  <div>
      <el-table
          ref="multipleTable"
          :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          @selection-change="handleSelectionChange"
          :header-cell-style="{background:'#eef1f6',color:'#606266'}"
          >
          <el-table-column
          type="selection"
          align="center"
          width="100">
          </el-table-column>
        <el-table-column label="" type="index" align="center" show-overflow-tooltip width="130px">
        </el-table-column>

          <el-table-column
          prop="title"
          label=""
          >
              <template slot-scope="scope">
                <div 
                @click="details(scope.row.id)"
                  style="cursor:pointer;"
                  >
                    {{scope.row.title}}
                  </div>
               </template>
          </el-table-column>

          <el-table-column
          align="center"
          prop="pid"
          label="id"
          >
          </el-table-column>

        <el-table-column
          prop="name"
          align="center"
          label=""
          >
          </el-table-column>
          <el-table-column
          align="center"
          label=""
          width="200"
          >

          <template slot-scope="scope" >
            <div v-if="scope.row.status==0"></div>
            <div v-else-if="scope.row.status==1"></div>
           </template>

          </el-table-column>
      </el-table>
        <el-pagination @size-change="handleSizeChange" 
              @current-change="handleCurrentChange" 
              :current-page="currentPage" 
              :page-sizes="pageSizes" 
              :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper" 
              :total="totalCount"
              class="paging">
        </el-pagination>
  </div>
</template>

<script>
    import axios from "axios";
export default {
  data () {
    return {
      list:[],
                // 
          tableData:[],
          // 
          currentPage:1,
          // ()
          totalCount:1,
          // 
          pageSizes:[10,20,50,100],
          // 
          PageSize:10,
     }
  },
  methods: {
    show() {
        this.$axios
            .post(process.env.HOST+`master/auth`)//`http://192.168.3.160:8080/bbs_war/master/auth`
            .then(res => {  
                this.list=res.data;
                   // tableData
                   this.tableData=res.data
                   // totalCount
                   this.totalCount=res.data.length
                console.log(res)   
          })
    },
     // 
        // 
       handleSizeChange(val) {
           //  
           this.PageSize=val
           // 
           this.currentPage=1
       },
         // 
       handleCurrentChange(val) {
           // 
           this.currentPage=val
       },
        //
	 handleSelectionChange(val) {
          this.multipleSelection=[];
          for(var i=0;i<val.length;i++){
              this.multipleSelection.push(val[i].id);
          }
              console.log(this.multipleSelection )
      },
      val(){
           this.value2=false
            console.log(this.value2)
      },
//
     details(id){
         console.log(id)
      },
  },
    created(){
       this.show();
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.paging{
    text-align: center;
    margin-top: 100px
}
</style>



